
.transactionHistory {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    font-size: 14px;
    border-spacing: 0;
    text-align: center;
    box-shadow:  5px 5px 3px 1px rgba(0, 0, 0, 0.3);
}

th {
    background: #BCEBDD;
    color: white;
    text-shadow: 0 1px 1px #2D2020;
    padding: 10px 80px;
}

th, td {
    border-style: solid;
    border-width: 0 1px 1px 0;
    border-color: white;
}

th:first-child, td:first-child {
    text-align: left;
}
    
th:last-child {
    border-right: none;
}

td {
    padding: 10px 80px;
    background: wheat;
}
  

   



